import React from 'react';
import {Box} from "@mui/material";
import Style from './Toggler.module.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMoon } from '@fortawesome/free-solid-svg-icons/faMoon';
import { faSun } from '@fortawesome/free-solid-svg-icons/faSun';



export default function Toggler({darkMode, handleClick}) {
    const transition = 'all 250ms ease'

   return (
      <Box fontSize={'1.5rem'} sx={{cursor: 'pointer', ":hover": {transform: 'translateY(-3px)', transition: transition}}}>
         {
            darkMode ?
               <span onClick={handleClick} aria-label="Full Moon" role="img"><FontAwesomeIcon icon={faSun} className={Style.iconDarkMode} /></span>
               :
               <span onClick={handleClick} aria-label="New Moon" role="img"><FontAwesomeIcon icon={faMoon} className={Style.iconDarkMode} /></span>
         }
      </Box>
   )
}